import React from 'react';


class TextToSpeech extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      selected_voice: null,
      items: []
    };
    
  }
  componentDidMount() {
        var available_voices;

        // list of languages is probably not loaded, wait for it
        if(window.speechSynthesis.getVoices().length == 0) {
	        window.speechSynthesis.addEventListener('voiceschanged', function() {
		        available_voices = window.speechSynthesis.getVoices();
                this.setState({
                              isLoaded: true,
                              items: available_voices,
                              selected_voice: 27
                });
	        });
        }
        else {
	        available_voices = window.speechSynthesis.getVoices();
            this.setState({
              isLoaded: true,
              items: available_voices,
              selected_voice: 27
            });
        }
  }

  handleChange(event) {
        let value = event.target.value;
            this.setState({
              isLoaded: true,
              selected_voice: value
            });
  }

  playText(text, index){
      var synth = window.speechSynthesis;
      var voices = synth.getVoices();;
      var utterThis = new SpeechSynthesisUtterance(text);
      utterThis.voice = voices[index] ;
      utterThis.pitch = 0;
      utterThis.rate = 0.7;
      synth.speak(utterThis);
  }

  render() {

      return (
        <div><select onChange={this.handleChange.bind(this)}> {this.state.items.map((value,index) =>  {
              if (index==27){
                return <option selected="selected" value={index}>{value.name}</option>
              }else{
                return <option value={index}>{value.name}</option>
              } 

          })}</select><button onClick={() => this.playText(this.props.text, this.state.selected_voice)}>play</button></div>
      );
  }
}


export default TextToSpeech;
